@charset "UTF-8";
@import "~@fontsource/poppins/index.css";
/* Tamaños Responsive*/
.contenido {
  width: 90%;
}

body {
  background-color: rgb(248, 248, 248);
  padding: 2rem 0;
  margin: 0;
}
body.darkmode {
  background-color: #090909;
}
body.darkmode .header__nombre h1 {
  color: white;
}
body.darkmode .header__profesion h3 {
  color: #cccccc;
}
body.darkmode .header__localidad h4 {
  color: #cccccc;
}
body.darkmode .navegacion {
  background-color: #060606;
  box-shadow: none;
}
body.darkmode .navegacion__enlaces a {
  color: #f2f2f2;
}
body.darkmode .navegacion__dark p {
  color: white;
}
body.darkmode .secciones__enlaces a {
  color: white;
}
body.darkmode .seccion__titulo h2,
body.darkmode .seccion h3 {
  color: white;
}
body.darkmode .seccion__texto p {
  color: #bfbfbf;
}
body.darkmode .seccion__row-subdata {
  color: #d9d9d9;
}
body.darkmode .seccion__card {
  background-color: rgb(19, 19, 19);
}
body.darkmode .swiper-button-next,
body.darkmode .swiper-button-prev {
  color: white !important;
}
body.darkmode .slides-container__slide-img {
  background-color: rgb(19, 19, 19);
}
body.darkmode .formulario label {
  color: white;
}
body.darkmode .formulario input {
  background-color: rgb(19, 19, 19);
  border: none;
  color: white;
}
body.darkmode .formulario__opciones {
  background-color: rgb(19, 19, 19);
  padding: 0;
}
body.darkmode .formulario__opcion {
  background-color: rgb(19, 19, 19);
  color: white;
}
body.darkmode .formulario__opcion:hover {
  background-color: black;
}
body.darkmode .formulario__aclaracion {
  color: white;
  background-color: rgb(19, 19, 19);
}
body.darkmode .formulario__modal {
  background-color: rgb(19, 19, 19);
  color: white;
}
body.darkmode .formulario__boton {
  background-color: rgb(19, 19, 19);
  color: white;
}
body.darkmode .formulario__recomendacion p {
  color: white;
}
body.darkmode .formulario__textarea textarea {
  background-color: rgb(19, 19, 19);
  border-color: rgb(19, 19, 19);
  color: white;
}

*::after,
*::before {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
label,
span,
ul,
input,
textarea,
button,
small, input {
  font-family: "Poppins", sans-serif !important;
}

.container {
  max-width: 60%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    max-width: 70%;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .container {
    max-width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1366px) {
  .container {
    max-width: 80%;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 90%;
    margin: 0 auto;
  }
}

.separador {
  display: block;
  border: 0.2px solid #939393;
  width: 80%;
}

img {
  width: 100%;
  display: block;
}

.swiper-button-next,
.swiper-button-prev {
  color: black !important;
  max-width: 30px;
}

.swiper {
  width: 90% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.swiper-slide {
  width: fit-content !important;
  margin-right: 3rem !important;
}

.swiper2 {
  overflow-x: hidden !important;
  cursor: grab;
}

.block {
  display: inline-block;
}

.shake {
  animation: shake 2s linear infinite;
}

.rotate {
  animation: rotate 1s linear;
}

.mostrar {
  opacity: 1 !important;
  visibility: visible !important;
}

.ocultar {
  opacity: 0;
  visibility: hidden;
}

.btn-cerrar {
  font-weight: 600;
  color: #4093ff;
  transition: color 0.2s ease;
}
.btn-cerrar:hover {
  cursor: pointer;
  color: #005fd9;
}

input[type=submit], button:hover {
  cursor: pointer;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes shake {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(10deg);
  }
  50% {
    transform: rotate(0eg);
  }
  75% {
    transform: rotate(-10deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
