.header__container {
  padding: 2rem 0rem;
  display: flex;
  align-items: center;
  gap: 5rem;
}
.header__foto, .header img {
  border-radius: 50%;
}
.header__foto {
  width: 250px;
}
.header__data {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.header__nombre h1 {
  font-size: 3rem;
  margin: 1rem 0;
  color: rgb(19, 19, 19);
}
.header__profesion h3 {
  color: rgb(61, 61, 61);
  font-weight: 500;
  margin: 0.5rem 0;
}
.header__localidad h4 {
  margin: 0;
  color: rgb(61, 61, 61);
  font-size: 1.1rem;
}

.navegacion {
  position: fixed;
  background-color: white;
  z-index: 10;
  right: 0;
  top: 0;
  height: 100vh;
  padding: 4rem 4rem;
  -webkit-box-shadow: 0px 0px 7px 3px #E1E1E1;
  box-shadow: 0px 0px 7px 3px #E1E1E1;
}
.navegacion__enlaces {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.navegacion__enlaces a {
  text-decoration: none;
  color: rgb(61, 61, 61);
  font-size: 1.1rem;
  background-color: transparent;
  border: 0px solid transparent;
}
.navegacion__dark {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.switch-button {
  display: inline-block;
}

.switch-button .switch-button__checkbox {
  display: none;
}

.switch-button .switch-button__label {
  background-color: #f2f2f2;
  width: 3rem;
  height: 1.5rem;
  border-radius: 3rem;
  display: inline-block;
  position: relative;
}

.switch-button .switch-button__label:before {
  transition: 0.2s;
  display: block;
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  background-color: white;
  content: "";
  border-radius: 50%;
  box-shadow: inset 0px 0px 0px 1px rgb(19, 19, 19);
}

.switch-button .switch-button__checkbox:checked + .switch-button__label {
  background-color: rgb(19, 19, 19);
}

.switch-button .switch-button__checkbox:checked + .switch-button__label:before {
  transform: translateX(1.5rem);
}

.secciones {
  padding: 3rem 0;
}
.secciones__enlaces a {
  text-decoration: none;
  color: rgb(19, 19, 19);
  font-size: 1rem;
  background-color: transparent;
  border: 0px solid transparent;
  margin-right: 3rem;
}
.secciones__enlaces {
  padding: 0 5rem;
}
.secciones__enlaces a:last-child {
  margin: 0;
}

.slides-container {
  overflow: hidden;
}
.slides-container__slide {
  display: flex;
  white-space: wrap;
  margin-block: 3rem;
}
.slides-container__slide-img {
  position: relative;
  padding: 1.5rem;
  height: 76px;
  width: 76px;
  margin-right: 3rem;
  background-color: white;
  border-radius: 50%;
}

.formulario__container {
  position: relative;
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-content: center;
  gap: 1rem;
}
.formulario input[type=text], .formulario input[type=email] {
  margin-bottom: 1.5rem;
}
.formulario__input {
  position: relative;
}
.formulario__busqueda {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  grid-column: 1/2;
}
.formulario__button--buscar {
  width: fit-content;
}
.formulario__aclaracion {
  position: absolute;
  width: fit-content;
  background-color: white;
  border-radius: 50%;
  padding: 0.5rem;
  font-weight: 700;
  right: 20%;
  top: -15%;
}
.formulario__aclaracion:hover {
  cursor: pointer;
}
.formulario__opciones {
  background-color: #fafafa;
  padding-block: 0.5rem;
  margin-top: 1rem;
}
.formulario__opcion {
  padding: 0.5rem;
  background-color: #f2f2f2;
  transition: 0.3s background ease-in-out;
}
.formulario__opcion:hover {
  cursor: pointer;
  background-color: white;
}
.formulario__modal {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  visibility: hidden;
  position: absolute;
  -webkit-box-shadow: 0px 0px 7px 3px #E1E1E1;
  box-shadow: 0px 0px 7px 3px #E1E1E1;
  background-color: white;
  z-index: 100;
  width: 0%;
  height: 150px;
}
.formulario__modal.mostrar {
  width: 70%;
}
.formulario__textarea textarea {
  resize: none;
  height: 100px;
}
.formulario__boton {
  all: unset;
  margin-top: 3rem;
  padding: 1rem;
  font-size: clamp(1rem, -0.8rem + 10vw, 0.8rem);
  background-color: white;
  color: rgb(19, 19, 19);
  width: fit-content;
  font-weight: 600;
}
.formulario__recomendacion {
  /* ESTRELLAS */
}
.formulario__recomendacion-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0;
}
.formulario__recomendacion .stars {
  display: flex;
  /* Al hacer hover permite que cambie la direccion del orden  de las estrellas. En vez de derecha a izquierda a derecha a izquierda*/
  gap: 3rem;
}
.formulario__recomendacion .stars input[type=radio] {
  display: none;
}
.formulario__recomendacion .stars input[type=radio]:checked ~ label {
  color: orange;
}
.formulario__recomendacion .stars label {
  transform: scale(1.5);
}
.formulario__recomendacion .stars label:hover ~ label {
  color: orange;
}
.formulario__recomendacion .stars label:hover {
  color: orange;
  cursor: pointer;
  animation: rotate 1s linear;
}

label {
  display: block;
  margin-block: 1.5rem;
}

/*INPUTS TEXT EMAIL STYLES*/
input[type=text], input[type=email], textarea {
  width: 80%;
  padding-block: 1rem;
  padding-inline-start: 0.5rem;
  font-size: clamp(0.9rem, -0.8rem + 10vw, 1rem);
  border: 0.2px solid;
  border-color: #dfdfdf;
  outline: none;
}

.cards__grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2rem;
}

.card {
  border: 1px solid black;
  width: fit-content;
  padding-inline: 2.5rem 2rem;
  padding-block: 2rem;
  display: flex;
}
.card__head {
  display: flex;
  flex-direction: column;
}
.card__persona-nombre {
  display: flex;
  gap: 0.3rem;
}
.card__persona-nombre p {
  font-size: clamp(1rem, -0.8rem + 10vw, 1.5rem);
  font-weight: 700;
  margin: 0;
  color: #202020;
}
.card__persona p {
  color: #2d2d2d;
}

.seccion {
  max-height: 1000px;
}
@media (min-width: 768px) {
  .seccion {
    max-height: 700px;
  }
}
@media (min-width: 1366px) {
  .seccion {
    max-height: 1000px;
  }
}
.seccion__titulo {
  font-size: 1.4rem;
  color: rgb(19, 19, 19);
}
.seccion__row {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.seccion__row-subdata {
  color: #535353;
  font-weight: 600;
}
.seccion__texto {
  text-align: justify;
  width: 90%;
}
.seccion__texto--proyectos {
  text-align: left;
}
.seccion__texto h3 {
  margin-top: 1.7rem;
  font-size: 1.4rem;
  margin-bottom: 0;
}
.seccion-titulos {
  overflow-y: hidden;
  max-height: 90%;
}
.seccion__card {
  margin-top: 5rem;
  background-color: #fafafa;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.seccion__card--proyectos {
  background-color: #f2f2f2;
  border-radius: 20px;
  transition: transform 0.2s ease;
  cursor: pointer;
}
.seccion__card--proyectos:hover {
  transform: scale(1.025);
}
.seccion__card-img {
  width: 400px;
}
.seccion__card-img--proyectos {
  max-height: 200px;
}
.seccion__card-img--proyectos img {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.seccion__card-img img {
  height: 100%;
  object-fit: cover;
}
.seccion__grid {
  padding-inline: 5rem;
}
.seccion__parrafo {
  line-height: 2rem;
  margin-bottom: 3rem;
  overflow: hidden;
  transition: height 0.4s ease;
}
.seccion__parrafo--proyectos {
  height: 200px;
}
.seccion__parrafo--expandido {
  height: 500px;
}
.seccion__guia {
  padding-block: 2rem;
  color: #4d4d4d;
}
.seccion__guia:hover {
  cursor: pointer;
}
.seccion.tecnologias {
  max-width: 100%;
}
.seccion__mark {
  padding: 0.2rem;
  border-radius: 50px;
}
.seccion__mark--celeste {
  background-color: rgb(115, 176, 255);
  color: white;
  font-weight: 700;
}